body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(231, 226, 235);
}


.lottie {
  width: 100vw;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


.App {
  font-family: sans-serif;
  text-align: center;
}

.container {
  width: 100vw;
  height: 100vh;
}

.cover {
  width: 110vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgb(105, 97, 157);
  transition: transform 0.5s ease;
  z-index: 999;

}

.cover.reveal {
  transform: translateY(-100%);

}

.list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s ease;
  color: black;
}

.list.reveal {
  opacity: 1;
}

.blank {
  border: transparent;
}